
.user-card{
    border: 1px solid;
    width: 250px;
    padding: 5px;
    margin: 5px;
    display: flex;
    align-items: center;
    flex-direction: column;
    box-shadow: 10px 10px 20px green;
}
.user-card:hover{
    transform: scale(1.1);
}
.img{
    width: 200px;
    height: 200px;
}